/* Fonts */
h1,h2,h3 {
    margin-bottom: 1rem;
}

/* Section Body changes */
.home-section .section-body {
    padding: 2.5rem;
    margin: 0 auto;
    max-width: 65rem;
}

/* Intro Section */
#intro {
    padding-top: 2.5rem;
    padding-bottom: 10rem;
    color: #fff;
    text-align: center;
    height: 100vh;
    background-color: var(--accent-color);
}

#intro p {
    font-size: 1.5rem
}

#intro h1 {
    font-size: 2.25rem!important
}

#intro a {
    width: 50%;
    margin: 0 auto
}

/* Skills Section */
#skills {
    margin-top: -10rem;
    text-align: center
}

#skills .box section {
    margin: 2rem 0
}

.skills-heading {
    margin: .5rem!important;
    color: var(--accent-color);
}

#skills p {
    margin: 0;
    padding: 0
}

#skills ul {
    list-style-type: none;
    margin: 0;
    padding: 0
}

/* Footer Section */
#footer {
    background-color: var(--accent-color);
    padding-top: 7.5rem
}

#footer,
#footer a svg {
    color: #fff;
    text-align: center
}

#footer a svg {
    height: 1.5rem;
    width: 2.25rem;
    padding: .25rem 0;
    margin: 5px;
    border: 2px solid #ff867f; /* TODO */
    border-radius: .25rem;
    transition: all .2s ease-in-out;
}

#footer a svg:hover {
    color: var(--accent-color);
    background-color: #fff; /* TODO */
    border-color: #fff
}
.box {
    display: flex;
    flex-direction: row;
    border: 1px solid var(--border-color);
    border-radius: 1rem;
    box-shadow: 0 3px 3px 0 rgba(0, 0, 0, .2);
    background-color: var(--lighter-background-color)
}

.box svg {
    color: var(--accent-color);
}

@media only screen and (max-width:750px) {
    .box {
        flex-direction: column
    }
}
/* Resume Section */
#resume {
    font-size: 1rem
}

#resume h1 {
    font-size: 2em;
}

/* Section body changes */
.resume-section .section-body {
    padding: 0;
    margin: 0; 
    width: 100%;
    height: 100%;
    box-sizing: inherit;
    display: flex;
    flex-direction: row;
}

@media only screen and (max-width: 750px) {
    .resume-section .section-body {
        flex-direction: column;
    }
}

.resume-section-body {
    float: right;
    height: 100%;
    display: block;
}

.resume-section h2 {
    float: left;
    min-width: 25rem;
    padding-bottom: 0;
    margin-bottom: 0;
}

.resume-section-body p {
    margin: 0;
}

.resume-section-item ul {
    padding-left: 1.25rem;
    padding-top: 0;
    margin-top: 0
}

.resume-section-item ul li {
    word-wrap: break-word
}

.resume-section-item h3 {
    margin-bottom: 0;
    color: var(--accent-color);
}

.resume-section-item h4 {
    margin-top: 0;
    margin-bottom: 0
}

.resume-button-link {
    text-decoration: none
}

.resume-button-link:hover {
    text-decoration: none;
    font-size: 1rem
}

.resume-button {
    text-align: left;
    max-width: 40rem;
    margin: 0 auto;
    transition: all .2s ease-in-out;
    margin-top: 3rem;
    margin-bottom: -10rem;
    box-shadow: 0 3px 3px 0 rgba(0, 0, 0, .2);
    position: relative;
    z-index: 100
}

@media only screen and (max-width:750px) {
    .resume-button {
        text-align: center
    }
}

.resume-button-text {
    margin-left: -15rem;
    margin-top: 3rem
}

@media only screen and (max-width:750px) {
    .resume-button-text {
        margin-left: 0;
        margin-top: -2rem
    }
}

.resume-button:hover {
    cursor: pointer;
    border-color: var(--accent-color);
}
.box article {
    flex-grow: 1;
    flex-basis: 0;
    -ms-flex-positive: 1;
    -ms-flex-preferred-size: 0;
    color: var(--text-color);
    display: block;
    padding: 2.5rem;
    border-right: 1px solid var(--border-color);
    border-bottom: 0 solid var(--border-color);
}

@media only screen and (max-width:750px) {
    .box article {
        border-right: 0 solid var(--border-color);
        border-bottom: 1px solid var(--border-color);
    }
}

.info-text input:focus {
    outline: none;
}

.info-body input {
    margin: 0;
    background-color: var(--background-color);
    color: var(--text-color);
    border: none;
    width: 90%;
    padding: 0;
    font-size: 1rem;
    font-family: 'VT323', monospace;
    overflow-wrap: break-word;
    word-break: break-all;
}

.info-text p {
    overflow-wrap: break-word;
    word-break: break-all;
}

.info-text p a {
    font-weight: bold;
    color: var(--text-color);
}
.info {
    width: 55vw;
    height: 100%;
    margin: 0 auto;
    margin-top: 7.5vh;
    padding-top: 1rem;
    background-color: #484244;
    border-radius: 6px;
    font-family: 'VT323', monospace;
    text-align: left;
    box-shadow: 4px 4px 4px 0 rgba(0, 0, 0, .2);
}

.info-body {
    background-color: var(--background-color);
    color: var(--text-color);
    padding: 1rem;
    margin: 0 auto;
    height: 100%;
    overflow: hidden;
}

.info-text {
    height: 40vh;
    width: 100%;
    overflow-y: scroll;
    scroll-behavior: smooth;
    padding-right: 3rem; /* Increase/decrease this value for cross-browser compatibility */
    box-sizing: content-box; /* So the width will be 100% + 17px */
}

.info-text p {
    font-size: 1rem !important;
    margin: 3px auto;
}

.info-carret {
    font-size: 1rem;
}
html {
  --border-color: rgba(0,0,0, 0.125); /* Safari */
  transition: opacity 2s;
  transition-timing-function: ease-in-out;
}

html[data-theme='dark'] {
  --background-color: #2E282A;
  --lighter-background-color: #484244;
  --accent-color: #ef3e36; /*#17BEBB;*/
  --text-color: #fff; /* Safari */
  transition: opacity 2s;
  transition-timing-function: ease-in-out;
}

html[data-theme='light'] {
  --background-color: #fff;
  --lighter-background-color: #fff;
  --accent-color: rgb(255, 82, 82);
  --text-color: #000; /* Safari */
  transition: opacity 2s;
  transition-timing-function: ease-in-out;
}

body {
  margin: 0;
  padding: 0;
  font-family: Roboto, sans-serif;
  font-weight: 300;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: var(--background-color);
  color: var(--text-color);
}

/* universal classes */
.no-border {
  border: none!important
}
